* {
  margin: 0;
  padding: 0;
}

html, body {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

#app {
  width: 100%;
  height: 100%;
  overflow: hidden;
  font-family: 'Open Sans';
}

.page-wrap {
  width: 100%;
  height: 100%;
}

.page {
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  -webkit-perspective: 1000;
  background-color: #FB5447;
  overflow: auto;
}

.long-page {
  width: 100%;
  min-height: 100%;
}

.page-bg-image {
  position: relative;
  width: 100%;
  height: 100%;
  background-size: 100%;
  /* background-position-y: 55%; */
  background-repeat: no-repeat;
  overflow: hidden;
}

.photo {
  position: absolute;
  top: 15px;
  width: 90%;
  left: 5%;
  height: 40%;
  animation: photoanimation 10s infinite;
  background-size: 100%;
  background-position-x: center;
  background-repeat: no-repeat;
}

@keyframes photoanimation {
  0% { background-size: 100%; }
  /* 25% { background-size: 110%; } */
  50% { background-size: 110%; }
  /* 75% { } */
  100% { background-size: 100%; }
}

.page3-content {
  position: relative;
  width: 100%;
  margin-top: 92vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.button-group {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.button {
  box-sizing: border-box;
  width: 26vw;
  height: 15.5vw;
  background-size: cover;
  margin: 1vw;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 10px 7px 10px;
  font-weight: bold;
  text-align: center;
  color: white;
  user-select: none;
  font-size: 4vw;
}
.button:active {
  opacity: 0.5;
}
.button.button-active {
  box-sizing: border-box;
  color: #DD241B;
  padding: 7px 10px 0 10px;
}

.input {
  width: 81vw;
  margin-top: 2.5vw;
  border-radius: 0;
  height: 13vw;
  border: 2.5px solid #FF9743;
  background: #FFF0D4;
  padding: 0 14px;
  outline: none;
}
.input::placeholder, .textarea::placeholder {
  color: #CC786D;
}

.textarea {
  width: 81vw;
  border-radius: 0;
  height: 30vw;
  border: 2.5px solid #FF9743;
  background: #FFF0D4;
  padding: 6px 6px;
  outline: none;
  resize: none;
}

.page4-content {
  position: relative;
  width: 100%;
  margin-top: 18vw;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.op {
  position: relative;
  box-sizing: border-box;
  width: 43vw;
  height: 43vw;
  background-size: contain;
  background-repeat: no-repeat;
  margin: 0.5vw;
}
.op:active {
  opacity: 0.5;
}
.op.op-active>.check {
  display: block;
}
.check {
  display: none;
  position: absolute;
  right: 0;
  bottom: 1vw;
  width: 9vw;
  height: 8vw;
  background-size: contain;
  background-repeat: no-repeat;
}

.confirm {
  position: relative;
  box-sizing: border-box;
  width: 36vw;
  height: 12vw;
  background-image: url(../Pages/images/confirm.png);
  background-size: contain;
  background-repeat: no-repeat;
  user-select: none;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  text-align: center;
  color: #DD241B;
  font-size: 5vw;
}
.confirm:active {
  opacity: 0.6;
}

.page5-content {
  position: relative;
  width: 100%;
  margin-top: 75vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.wallet-card {
  position: relative;
  box-sizing: border-box;
  width: 76vw;
  height: 60vw;
  background-image: url(../Pages/images/card.png);
  background-size: contain;
  background-repeat: no-repeat;
  user-select: none;
}
.wallet-card:active {
  opacity: 0.8;
  background-image: url(../Pages/images/card-down.png);
}
.wallet-phone {
  color: white;
  position: absolute;
  top: 32%;
  left: 33%;
  font-size: 4.5vw;
  line-height: 4.5vw;
}
.wallet-address {
  color: white;
  position: absolute;
  top: 44.5%;
  left: 41%;
  font-size: 4.5vw;
  line-height: 4.5vw;
}

.arrow {
  position: absolute;
  left: 50%;
  bottom: 15px;
  width: 100px;
  height: 20px;
  margin-left: -50px;
  background-size: 100px auto;
  background-repeat: no-repeat;
  background-image: url(../Pages/images/arrow.png);
  opacity: 0;
}

.current .arrow {
  animation: arrow 2s ease infinite;
}

@keyframes arrow {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, 5px, 0);
  }
  70% {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
  }
  100% {
    opacity: 0;
    -webkit-transform: translate3d(0, -5px, 0);
  }
}
